    var bagDropbtn_content = document.querySelector('.bagDropbtn_content');
    var bagDropbtn_click = document.querySelector('.bagDropbtn_click');
    var bagDropbtn = document.querySelector('.bagDropbtn');

    var i;
    for (i = 0; i < bagDropdowns.length; i++) {
      var openbagDropdown = bagDropdowns[i];
      if (openbagDropdown.classList.contains('show')) {
        openbagDropdown.classList.remove('show');
      }
    }
  }
}


